.item {
    position: relative;
    width: 380px;
    height: 460px;
    justify-content: center;
    background: linear-gradient(114.99deg,
     #471ca9 -0.99%,
     #5736a3 54.28%,
     #4b2a99 78.99%);
    box-shadow: -2.5777px 6.87386px 20.6216px rgba(0, 0, 0, 0.23);
    border-radius: 20px;
}

.item::after {
    position: absolute;
    content: '';
    width: 100%;
    height: 8px;
    top: 214px;
    background: #ebd8ff;
    box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.06),
            inset 0px -1.71846px 3.43693px #ae7be3, inset 0px 3.43693px 2.5777px #fbf8ff;
}

.logo {
    position: absolute;
    left: 20px;
    top: 20px;
    width: 76px;
    height: 22px;
}

.picture {
    width: 308px;
    height: 168px;
    margin-left:36px;
    margin-top: 28px;
}

.avatar {
    position: relative;
    margin-left:150px;
    margin-top: -18px;
    z-index: 11;
    background: #ebd8ff;
    box-shadow: 0px 4.39163px 4.39163px rgba(0, 0, 0, 0.06),
        inset 0px -2.19582px 4.39163px #ae7be3,
        inset 0px 4.39163px 3.29372px #fbf8ff;
    border-radius: 86px;
    border: 9px solid #ebd8ff;
}

.tweets {
    margin-top: 26px;
    text-align: center;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.2;
    text-transform: uppercase;
    color: #ebd8ff;
}

.followers {
    margin-top: 16px;
    text-align: center;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.2;
    text-transform: uppercase;
    color: #ebd8ff;
}

button[class='active'] {
    background-color: #5cd3a8;
    display: block;
    margin: 0 auto;
    margin-top: 26px;
    width: 196px;
    height: 50px;
    font-weight: 600;
    font-size: 18px;
    line-height: 1.22;
    text-transform: uppercase;
    box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    color: #373737;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

button[class='passive'] {
    background: #c1a0e3;
    display: block;
    margin: 0 auto;
    margin-top: 26px;
    width: 196px;
    height: 50px;
    font-weight: 600;
    font-size: 18px;
    line-height: 1.22;
    text-transform: uppercase;
    box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    color: #373737;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

button[class='passive']:hover,
button[class='active']:hover {
    background-color: #5cd3a8;
    transform: scale(1.05);
}